.App {
    width: 100vw;
    height: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
}

.book_container {
    width: 250px;
    border-radius: 10px;
    background-color: white;
    box-shadow: 0 4px 8px rgb(0 0 0 /25%);
}

.book_img {
    width: 250px;
    height: 300px;
    object-fit: cover;
    border-radius: 10px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}

.book_name {
    font-size: 20px;
    font-weight: bold;
    margin: 5px 0;
    text-align: center;
}

.book_author {
    font-size: 16px;
    font-weight: 500;
    margin: 5px 0;
    text-align: center;
}

.book_price {
    font-size: 16px;
    font-weight: 500;
    margin: 5px 0;
    text-align: center;
}

.book_price>span {
    color: #21a700;
}

.buy_btn {
    width: 100%;
    height: 50px;
    outline: none;
    border: none;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: #f4d072;
    margin: 10px;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}